<script>
	import Vue from "vue";
	import AppIcons from "./AppIcons.vue";

	// const ERROR_LIST = [
	// 	{hasLength: "Must contain at least 8 characters long."},
	// 	{
	// 		hasAlphaChars: "Must contain at least one UPPERCASE and one lowercase character.",
	// 		goodValue: true,
	// 	},
	// 	{hasNumber: "Must contain at least one number."},
	// 	{hasSpecial: "Must contain at least one special character."},
	// 	{
	// 		noIdenticalChars: "Must not contain more than two identical characters in a row.",
	// 	},
	// 	{noIncrementingNumbers: "Must not contain incrementing numbers."},
	// 	{
	// 		noReservedWords:
	// 			"Must not contain your given names, username, email address, or the words affinity or 'pass.",
	// 	},
	// ];

	let PasswordPage = Vue.extend({
		name: "PasswordPage",
		props: ["passwordVal", "confirmPasswordVal", "loading", "isMFAV2"],
		components: {
			AppIcons,
		},
		data() {
			return {
				output: {
					password: "",
					confirmPassword: "",
					passed: undefined,
				},
				hasLength: false,
				hasNumber: false,
				hasAlphaChars: false,
				hasSpecial: false,
				noIncrementingNumbers: false,
				noIdenticalChars: false,
				noReservedWords: false,

				showPwdGuide: false,
				showRpwdGuide: false,
				showPwd: false,
				showRePwd: false,
				outFocusMessage: "",
			};
		},
		watch: {
			"output.password": function () {
				this.outFocusMessage = "";
				if (this.isMFAV2) {
					this.hasLength = this.output.password.length >= 8;
					this.hasNumber = /\d/.test(this.output.password);
					this.hasAlphaChars =
						/[a-z]/.test(this.output.password) && /[A-Z]/.test(this.output.password);
					this.hasSpecial = /[!@#\$%\^\&*\)\(+=._-]/.test(this.output.password);

					const {password} = this.output;
					const strArr = password.match(/.{1,1}/g);

					this.checkForIncrementingNumbers(strArr);
					this.checkForSameCharacters(strArr);
					this.output.passed = ![
						this.hasAlphaChars,
						this.hasLength,
						this.hasNumber,
						this.hasSpecial,
						this.noIdenticalChars,
						this.noIncrementingNumbers,
					].includes(false);
				} else this.output.passed = true;
				// ERROR_LIST.forEach((error) => {
				// 	const key = Object.keys(error)[0];
				// 	if (!this[key] && !this.outFocusMessage) {
				// 		this.outFocusMessage = error[key];
				// 	}
				// });
				this.$emit("input", this.output);
			},
			"output.confirmPassword": function () {
				this.$emit("input", this.output);
			},
		},
		methods: {
			handlePwdFocus() {
				this.showPwdGuide = this.isMFAV2 ? true : false;
			},
			handlePwdBlur() {
				if (this.isMFAV2) {
					this.showPwdGuide = false;
					this.validatePassword();
				}
			},
			toggleEye() {
				this.showPwd = !this.showPwd;
			},
			toggleEyeRePwd() {
				this.showRePwd = !this.showRePwd;
			},
			checkForIncrementingNumbers(strArr) {
				this.noIncrementingNumbers = true;
				if (this.output.password && strArr.length >= 3) {
					const nums = "0123456789";
					// Convert the password string to an array of characters
					strArr.forEach((_, index) => {
						const partialStr = strArr.slice(index, index + 3).join("");
						// Check if the next character is the next number in the sequence
						if (nums.includes(partialStr) && index + 2 < strArr.length) {
							this.noIncrementingNumbers = false;
						}
					});
				}
			},
			checkForSameCharacters(strArr) {
				this.noIdenticalChars = true;
				if (this.output.password && strArr.length >= 3) {
					let count = 0,
						mainChar = "";
					strArr.forEach((char) => {
						if (char === mainChar) {
							count++;
						} else {
							count = 1;
							mainChar = char;
						}

						if (count > 2) {
							this.noIdenticalChars = false;
						}
					});
				}
			},
			async validatePassword() {
				const {password} = this.output;
				if (password && this.isMFAV2) {
					const res = await this.$ajax({
						method: "GET",
						url: "/Password/validate",
						params: {password: encodeURIComponent(password)},
					});
					const {result} = res.data;
					if (result === "FAILED") {
						this.noReservedWords = false;
						this.outFocusMessage = res.data.message;
					} else this.noReservedWords = true;
				}
			},
		},
	});
	Vue.component("passwordPage", PasswordPage);
	export default PasswordPage;
</script>

<style scoped>
	.newui .section .default-form .form-row input.long {
		max-width: 100%;
		width: 100%;
	}

	.reset-pwd-page {
		padding: 20px 0;
	}

	.password-ctrl-input {
		position: relative;
		z-index: 8;
	}

	.password-ctrl-input.repwd {
		z-index: 7;
	}

	.password-ctrl-input.pwd > input {
		position: relative;
		z-index: 11;
	}

	.password-ctrl-guide {
		position: absolute;
		top: calc(100% - 8px);
		left: 0;
		right: 0;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
		min-height: 60px;
		overflow: hidden;
		box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
		background-color: #fff;
		z-index: 10;
		margin-left: -12px;
		margin-right: -12px;
	}

	.password-ctrl-guide > div {
		padding: 12px 20px 8px;
	}

	ul.password-guide-list {
		margin: 0;
		padding-left: 0;
		list-style: none;
	}

	ul.password-guide-list > li {
		padding-left: 20px;
		position: relative;
		line-height: 1.5;
	}

	span.password-ctrl-icon {
		background-size: cover;
		background-position: center;
		position: absolute;
		width: 14px;
		height: 14px;
	}

	span.eye-toggle {
		position: absolute;
		right: 14px;
		top: 14px;
		font-size: 16px;
		cursor: pointer;
		z-index: 11;
	}

	ul.password-guide-list > li span.validation-ico {
		background-image: url("../assets/outline-close.svg");
		left: 0;
		top: 4px;
	}

	ul.password-guide-list > li.validation--passed {
		color: #bababa;
	}

	ul.password-guide-list > li.validation--passed span.validation-ico {
		background-image: url("../assets/outline-check.svg");
	}

	.password-ctrl-guide ul li {
		line-height: 1.75;
	}

	ul.pwd-error {
		position: absolute;
		bottom: 100%;
		right: 0;
		left: 0;
		background-color: #fff4f4;
		border-radius: 10px;
		padding: 8px 12px;
		margin-bottom: 8px;
	}

	ul.pwd-error li {
		font-size: 10px;
	}
	ul.pwd-error > li span.validation-ico {
		top: 0;
	}
</style>

<template>
	<div class="default-form w-border" style="text-align: left; margin: auto">
		<div class="form-row password-ctrl-input pwd">
			<input
				maxlength="50"
				placeholder="Enter your new password"
				v-model="output.password"
				@focus="handlePwdFocus"
				@blur="handlePwdBlur"
				v-bind:type="showPwd ? 'text' : 'password'"
				autocomplete="off"
				class="long" />
			<span class="eye-toggle" @click="toggleEye">
				<appIcons asset="eye-invisible" v-if="!showPwd" />
				<appIcons asset="eye" v-if="showPwd" />
			</span>
			<div class="password-ctrl-guide" v-show="showPwdGuide">
				<div>
					<ul class="password-guide-list">
						<li :class="{'validation--passed': hasLength}">
							<span class="password-ctrl-icon validation-ico"></span>
							<b>Must contain</b> at least 8 characters long.
						</li>
						<li :class="{'validation--passed': hasAlphaChars}">
							<span class="password-ctrl-icon validation-ico"></span>
							<b>Must contain</b> at least one UPPERCASE and one lowercase character.
						</li>
						<li :class="{'validation--passed': hasNumber && hasSpecial}">
							<span class="password-ctrl-icon validation-ico"></span>
							<b>Must contain</b> at least one number or special character (@#$%&*!)
						</li>
						<li :class="{'validation--passed': noIdenticalChars}">
							<span class="password-ctrl-icon validation-ico"></span><b>Must not contain</b> more
							than two identical characters in a row (aaa, 444).
						</li>
						<li :class="{'validation--passed': noIncrementingNumbers}">
							<span class="password-ctrl-icon validation-ico"></span
							><b>Must not contain</b> incrementing numbers (1234).
						</li>

						<li :class="{'validation--passed': noReservedWords}">
							<span class="password-ctrl-icon validation-ico"></span><b>Must not contain</b> your
							given names, username, email address, or the words affinity or 'pass.
						</li>
					</ul>
				</div>
			</div>
			<ul class="password-guide-list pwd-error" v-show="outFocusMessage !== '' && !showPwdGuide">
				<li><span class="password-ctrl-icon validation-ico"></span>{{ outFocusMessage }}</li>
			</ul>
		</div>
		<div class="form-row password-ctrl-input repwd">
			<input
				maxlength="50"
				v-bind:type="showRePwd ? 'text' : 'password'"
				placeholder="Re-enter your new password"
				v-model="output.confirmPassword"
				autocomplete="off"
				class="long" />
			<span class="eye-toggle" @click="toggleEyeRePwd">
				<appIcons asset="eye-invisible" v-if="!showRePwd" />
				<appIcons asset="eye" v-if="showRePwd" />
			</span>
		</div>
		<div class="form-row" style="text-align: center">
			<div class="spinner small light" v-show="loading"></div>
			<input type="submit" value="Continue" class="button large blue" v-show="!loading" v-cloak />
		</div>
	</div>
</template>
